import { Injector } from '../di/injector';
import { ViewDefinition, ViewDefinitionFactory, ViewData } from './types';
import { createInjector } from './refs';

const /** @type {?} */ VIEW_DEFINITION_CACHE = new WeakMap<any, ViewDefinition>();
/**
 * @param {?} factory
 * @return {?}
 */
export function resolveViewDefinition(factory: ViewDefinitionFactory): ViewDefinition {
  let /** @type {?} */ value: ViewDefinition = VIEW_DEFINITION_CACHE.get(factory);
  if (!value) {
    value = factory();
    VIEW_DEFINITION_CACHE.set(factory, value);
  }
  return value;
}
