import { PlatformAlreadyExistsError } from './platform_errors';
import { Injector } from '../di/injector';
import { InjectionToken } from '../di/injection_token';
import { Provider } from '../di/provider';
import { ReflectiveInjector } from '../di/reflective_injector';
import { ClassType } from '../type';
import { ComponentFactory } from '../component/factory';

let /** @type {?} */ _platform: PlatformRef;
/**
 * @abstract
 */
export abstract class PlatformRef {
/**
 * @abstract
 * @template C
 * @param {?} component
 * @return {?}
 */
bootstrapComponent<C>(component: ClassType<C>) {}
/**
 * @abstract
 * @return {?}
 */
injector() {}
/**
 * @abstract
 * @param {?} callback
 * @return {?}
 */
onDestroy(callback: () => void) {}
/**
 * @abstract
 * @return {?}
 */
destroy() {}
/**
 * @abstract
 * @return {?}
 */
destroyed() {}
}
/**
 * @return {?}
 */
export function getPlatform(): PlatformRef {
  return _platform && !_platform.destroyed ? _platform : null;
}
/**
 * @param {?=} providers
 * @return {?}
 */
export function createPlatformFactory(providers: Provider[] = []):
  (extraProviders?: Provider[]) => PlatformRef {
  return (extraProviders: Provider[] = []) => {
    if (getPlatform()) {
      throw new PlatformAlreadyExistsError();
    }

    const /** @type {?} */ injector = ReflectiveInjector.resolveAndCreate(providers.concat(extraProviders));
    _platform = injector.get(PlatformRef);
    return _platform;
  };
}
