export interface Visitor {
  visitElement(element: Element, context: any): any;
  visitAttribute(element: Element, attr: Attr, context: any): any;
  visitText(text: Text, context: any): any;
  visitComment(comment: Comment, context: any): any;
}

export interface RendererType {
  visitor: Visitor;
  data: {[kind: string]: any};
}
/**
 * \@experimental
 * @abstract
 */
export abstract class RendererFactory {
/**
 * @abstract
 * @param {?} hostElement
 * @param {?} type
 * @return {?}
 */
createRenderer(hostElement: any, type: RendererType) {}
}
/**
 * @abstract
 */
export abstract class Renderer {
/**
 * @abstract
 * @param {?} context
 * @return {?}
 */
parse(context: any) {}
/**
 * @abstract
 * @return {?}
 */
destroy() {}
/**
 * @abstract
 * @param {?} name
 * @param {?=} namespace
 * @return {?}
 */
createElement(name: string, namespace?: string) {}
/**
 * @abstract
 * @param {?} value
 * @return {?}
 */
createComment(value: string) {}
/**
 * @abstract
 * @param {?} value
 * @return {?}
 */
createText(value: string) {}
/**
 * @abstract
 * @param {?} node
 * @return {?}
 */
destroyNode(node: any) {}
/**
 * @abstract
 * @param {?} parent
 * @param {?} newChild
 * @return {?}
 */
appendChild(parent: any, newChild: any) {}
/**
 * @abstract
 * @param {?} parent
 * @param {?} newChild
 * @param {?} refChild
 * @return {?}
 */
insertBefore(parent: any, newChild: any, refChild: any) {}
/**
 * @abstract
 * @param {?} parent
 * @param {?} oldChild
 * @return {?}
 */
removeChild(parent: any, oldChild: any) {}
/**
 * @abstract
 * @param {?} selectorOrNode
 * @return {?}
 */
selectRootElement(selectorOrNode: string|any) {}
/**
 * @abstract
 * @param {?} node
 * @return {?}
 */
parentNode(node: any) {}
/**
 * @abstract
 * @param {?} node
 * @return {?}
 */
nextSibling(node: any) {}
/**
 * @abstract
 * @param {?} el
 * @param {?} name
 * @param {?} value
 * @param {?=} namespace
 * @return {?}
 */
setAttribute(el: any, name: string, value: string, namespace?: string) {}
/**
 * @abstract
 * @param {?} el
 * @param {?} name
 * @param {?=} namespace
 * @return {?}
 */
removeAttribute(el: any, name: string, namespace?: string) {}
/**
 * @abstract
 * @param {?} el
 * @param {?} name
 * @return {?}
 */
addClass(el: any, name: string) {}
/**
 * @abstract
 * @param {?} el
 * @param {?} name
 * @return {?}
 */
removeClass(el: any, name: string) {}
/**
 * @abstract
 * @param {?} el
 * @param {?} style
 * @param {?} value
 * @param {?} hasVendorPrefix
 * @param {?} hasImportant
 * @return {?}
 */
setStyle(
      el: any, style: string, value: any, hasVendorPrefix: boolean, hasImportant: boolean) {}
/**
 * @abstract
 * @param {?} el
 * @param {?} style
 * @param {?} hasVendorPrefix
 * @return {?}
 */
removeStyle(el: any, style: string, hasVendorPrefix: boolean) {}
/**
 * @abstract
 * @param {?} el
 * @param {?} name
 * @param {?} value
 * @return {?}
 */
setProperty(el: any, name: string, value: any) {}
/**
 * @abstract
 * @param {?} node
 * @param {?} value
 * @return {?}
 */
setValue(node: any, value: string) {}
/**
 * @abstract
 * @param {?} target
 * @param {?} eventName
 * @param {?} callback
 * @return {?}
 */
listen(
      target: 'window'|'document'|'body'|any, eventName: string,
      callback: (event: any) => boolean | void) {}
}
